html {
  font-size: 10px;
}
body {
  font-family: 'Source Sans Pro', sans-serif;
  color: rgb(var(--dark-persian-blue));
  font-size: 1.4rem;
  font-weight: normal;
  line-height: 1.6;
  letter-spacing: 0.3px;
  background: #FAFBFD;
}

#root {
  height: 100%;
}

.rootContainer {
  height: 100%;
  overflow: hidden;
}

/* style guide common character css */
h1 {
  font-size: 3.2rem;
  font-weight: 600;
  letter-spacing: -0.6px;
  line-height: normal;
  line-height: initial;
}

h2 {
  font-size: 2.4rem;
  font-weight: 600;
  letter-spacing: -0.4px;
  line-height: normal;
  line-height: initial;
}

h3 {
  font-size: 2rem;
  font-weight: 600;
  letter-spacing: normal;
  font-style: normal;
  font-stretch: normal;
}

h4 {
  font-size: 1.6rem;
  font-weight: 600;
  letter-spacing: normal;
  line-height: normal;
  line-height: initial;
}

h5 {
  font-size: 1.4rem;
  font-weight: 600;
  letter-spacing: 0.2px;
}

h6 {
  font-size: 1.3rem;
  font-weight: 600;
  letter-spacing: 0.3px;
  line-height: normal;
  line-height: initial;
}

.input {
  font-family: 'Source Sans Pro';
  font-size: 1.5rem;
  letter-spacing: 0.3px;
  border-radius: 3px;
  padding: 0 1rem;
  border: 1px solid rgba(var(--dusk), 0.6);
}

label, .label {
  font-size: 1.3rem;
  font-weight: 300;
  line-height: 1.6;
  letter-spacing: 0.4px;
}

input::-webkit-input-placeholder {
  font-size: 1.3rem;
  letter-spacing: 0.3px;
  color:  rgb(var(--bluey-grey));
}

input::-ms-input-placeholder {
  font-size: 1.3rem;
  letter-spacing: 0.3px;
  color:  rgb(var(--bluey-grey));
}

input::placeholder {
  font-size: 1.3rem;
  letter-spacing: 0.3px;
  color:  rgb(var(--bluey-grey));
}

small {
  font-size: 1.2rem;
  font-weight: normal;
  letter-spacing: 0.2px;
  color:  rgb(var(--bluey-grey));
}

.link {
  color:  rgb(var(--dodger-blue));
  cursor: pointer;
}

.truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
          line-clamp: 3;
  box-orient: vertical;
}

/*base font css*/
